import React from "react";

import PinDropIcon from "@material-ui/icons/PinDrop";

const Delivery = ({ rating }) => {
   return (
      <>
         <div>
            <span>Delivery Date:</span>
            <span>2 days after ordering</span>
         </div>
         <div>
            <span>
               Will be delivered by
               <span
                  className="shopName"
                  onClick={() =>
                     alert(
                        "You'll be redirected to the delivery shop's website"
                     )
                  }
               >
                  Omar's Electronics
               </span>
               ({rating} out of 5 stars) 10,000 ratings
            </span>
         </div>
         <div>
            <span className="icon">
               <PinDropIcon />
            </span>
            <span>Delivered to 30205 customers</span>
         </div>
      </>
   );
};

export default Delivery;
