import React from "react";

import { adsPath } from "./ImageData";

const Ads = () => {
   return (
      <div className="ads container pt-3">
         <div className="ads__wrapper mx-5 my-3 row">
            {adsPath.map((path, index) => {
               return (
                  <div
                     className="wrapper__singleAd my-2 col-lg-6 col-md-12 col-12"
                     key={index}
                  >
                     <img
                        src={process.env.PUBLIC_URL + path}
                        alt="Ad photo"
                        className="img-fluid"
                     />
                  </div>
               );
            })}
         </div>
      </div>
   );
};

export default Ads;
