import React, { useRef, useState, useEffect } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { Link } from "react-router-dom";

import { Button } from "@material-ui/core";

import { productsPath } from "./ImageData";
import Ratings from "./Ratings";

import { useSelector, useDispatch } from "react-redux";
import { addToCart, refresh } from "../redux/actions";

const SpecialPrice = () => {
   const { addedToCart, doRefresh } = useSelector((state) => state);
   const dispatch = useDispatch();

   const [allCartItemID, setAllCartItemID] = useState([]);
   const [filteredItems, setFilteredItems] = useState([]);
   const [windowWidth, setWindowWidth] = useState(window.innerWidth);

   const allBrandsBtnRef = useRef(null);
   const appleBtnRef = useRef(null);
   const redmiBtnRef = useRef(null);
   const samsungBtnRef = useRef(null);

   useEffect(() => {
      setFilteredItems(productsPath);
      allBrandsBtnRef.current.classList.add("active");
   }, []);

   const filterProductByBrand = (brand) => {
      if (brand === "AllBrand") {
         setFilteredItems(productsPath);
      } else {
         const obj = productsPath.filter((product) => {
            return product.brandName === brand;
         });
         setFilteredItems(obj);
      }
   };

   const handleBrandBtn = (btnName) => {
      filterProductByBrand(btnName);

      if (btnName === "AllBrand") {
         allBrandsBtnRef.current.classList.add("active");
      } else {
         allBrandsBtnRef.current.classList.remove("active");
      }
      if (btnName === "Apple") {
         appleBtnRef.current.classList.add("active");
      } else {
         appleBtnRef.current.classList.remove("active");
      }
      if (btnName === "Redmi") {
         redmiBtnRef.current.classList.add("active");
      } else {
         redmiBtnRef.current.classList.remove("active");
      }
      if (btnName === "Samsung") {
         samsungBtnRef.current.classList.add("active");
      } else {
         samsungBtnRef.current.classList.remove("active");
      }
   };

   const handleAddToCart = (id, name, brand, rating, path, price) => {
      dispatch(
         addToCart({
            id: id,
            color: "white",
            quantity: 1,
            name: name,
            brand: brand,
            rating: rating,
            path: path,
            price: price,
         })
      );
   };

   const sliderSettings = {
      dots: true,
      arrows: false,
      infinite: false,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
   };

   useEffect(() => {
      let arr = [];
      addedToCart.forEach((product) => {
         arr.push(product.id);
      });
      setAllCartItemID(arr);
      dispatch(refresh(false));
   }, [addedToCart, doRefresh]);

   useEffect(() => {
      window.addEventListener("resize", () => {
         setWindowWidth(window.innerWidth);
      });

      return () => {
         window.removeEventListener("resize", () => {
            setWindowWidth(window.innerWidth);
         });
      };
   }, []);

   return (
      <div className="specialPrice container my-5">
         <div className="specialPrice__title">
            <span>Special Price</span>
         </div>
         <div className="specialPrice__brandBtn">
            <Button
               ref={allBrandsBtnRef}
               onClick={() => handleBrandBtn("AllBrand")}
               className="button"
            >
               All Brands
            </Button>
            <Button
               ref={appleBtnRef}
               onClick={() => handleBrandBtn("Apple")}
               className="button"
            >
               Apple
            </Button>
            <Button
               ref={redmiBtnRef}
               onClick={() => handleBrandBtn("Redmi")}
               className="button"
            >
               Redmi
            </Button>
            <Button
               ref={samsungBtnRef}
               onClick={() => handleBrandBtn("Samsung")}
               className="button"
            >
               Samsung
            </Button>
         </div>
         <div className="specialPrice__products">
            <div
               className={
                  window.innerWidth > 500
                     ? "products__wrapper largeScreen"
                     : "products__wrapper"
               }
            >
               {windowWidth > 500 ? (
                  filteredItems.map((product, index) => {
                     let matching = 0;
                     allCartItemID.forEach((id) => {
                        if (id === product.id) {
                           matching += 1;
                        }
                     });

                     return (
                        <div
                           className="singleProduct animate__animated animate__zoomIn animate__faster"
                           key={index}
                        >
                           <div className="image">
                              <Link to={`/product/${product.id}`}>
                                 <img
                                    src={
                                       process.env.PUBLIC_URL + product.pathName
                                    }
                                    alt="product photo"
                                 />
                              </Link>
                           </div>
                           <div className="productName">
                              <span>{product.name}</span>
                           </div>
                           <div className="ratings">
                              <Ratings rating={product.rating} />
                           </div>
                           <div className="productPrice">
                              <strong>$</strong>
                              <strong>{product.price}</strong>
                           </div>
                           <div className="addToCart_Btn">
                              {matching > 0 ? (
                                 <Button
                                    variant="contained"
                                    className="addedToCart"
                                    disabled
                                 >
                                    Added To Cart
                                 </Button>
                              ) : (
                                 <Button
                                    variant="contained"
                                    onClick={() =>
                                       handleAddToCart(
                                          product.id,
                                          product.name,
                                          product.brandName,
                                          product.rating,
                                          product.pathName,
                                          product.price
                                       )
                                    }
                                 >
                                    Add To Cart
                                 </Button>
                              )}
                           </div>
                        </div>
                     );
                  })
               ) : (
                  <Slider {...sliderSettings}>
                     {filteredItems.map((product, index) => {
                        let matching = 0;
                        allCartItemID.forEach((id) => {
                           if (id === product.id) {
                              matching += 1;
                           }
                        });

                        return (
                           <div className="singleProduct" key={index}>
                              <div className="image">
                                 <Link to={`/product/${product.id}`}>
                                    <img
                                       src={
                                          process.env.PUBLIC_URL +
                                          product.pathName
                                       }
                                       alt="product photo"
                                    />
                                 </Link>
                              </div>
                              <div className="productName">
                                 <span>{product.name}</span>
                              </div>
                              <div className="ratings">
                                 <Ratings rating={product.rating} />
                              </div>
                              <div className="productPrice">
                                 <strong>$</strong>
                                 <strong>{product.price}</strong>
                              </div>
                              <div className="addToCart_Btn">
                                 {matching > 0 ? (
                                    <Button
                                       variant="contained"
                                       className="addedToCart"
                                       disabled
                                    >
                                       Added To Cart
                                    </Button>
                                 ) : (
                                    <Button
                                       variant="contained"
                                       onClick={() =>
                                          handleAddToCart(
                                             product.id,
                                             product.name,
                                             product.brandName,
                                             product.rating,
                                             product.pathName,
                                             product.price
                                          )
                                       }
                                    >
                                       Add To Cart
                                    </Button>
                                 )}
                              </div>
                           </div>
                        );
                     })}
                  </Slider>
               )}
            </div>
         </div>
      </div>
   );
};

export default SpecialPrice;
