import React from "react";

import StarIcon from "@material-ui/icons/Star";
import StarBorderIcon from "@material-ui/icons/StarBorder";

const Ratings = (props) => {
   if (props.rating === 0) {
      return (
         <>
            <span>
               <StarBorderIcon />
            </span>
            <span>
               <StarBorderIcon />
            </span>
            <span>
               <StarBorderIcon />
            </span>
            <span>
               <StarBorderIcon />
            </span>
            <span>
               <StarBorderIcon />
            </span>
         </>
      );
   } else if (props.rating === 1) {
      return (
         <>
            <span>
               <StarIcon />
            </span>
            <span>
               <StarBorderIcon />
            </span>
            <span>
               <StarBorderIcon />
            </span>
            <span>
               <StarBorderIcon />
            </span>
            <span>
               <StarBorderIcon />
            </span>
         </>
      );
   } else if (props.rating === 2) {
      return (
         <>
            <span>
               <StarIcon />
            </span>
            <span>
               <StarIcon />
            </span>
            <span>
               <StarBorderIcon />
            </span>
            <span>
               <StarBorderIcon />
            </span>
            <span>
               <StarBorderIcon />
            </span>
         </>
      );
   } else if (props.rating === 3) {
      return (
         <>
            <span>
               <StarIcon />
            </span>
            <span>
               <StarIcon />
            </span>
            <span>
               <StarIcon />
            </span>
            <span>
               <StarBorderIcon />
            </span>
            <span>
               <StarBorderIcon />
            </span>
         </>
      );
   } else if (props.rating === 4) {
      return (
         <>
            <span>
               <StarIcon />
            </span>
            <span>
               <StarIcon />
            </span>
            <span>
               <StarIcon />
            </span>
            <span>
               <StarIcon />
            </span>
            <span>
               <StarBorderIcon />
            </span>
         </>
      );
   } else if (props.rating === 5) {
      return (
         <>
            <span>
               <StarIcon />
            </span>
            <span>
               <StarIcon />
            </span>
            <span>
               <StarIcon />
            </span>
            <span>
               <StarIcon />
            </span>
            <span>
               <StarIcon />
            </span>
         </>
      );
   } else {
      return null;
   }
};

export default Ratings;
