import React, { useRef, useEffect, useState } from "react";
import { HashRouter, Route, Switch, Redirect } from "react-router-dom";

import MenuOpenIcon from "@material-ui/icons/MenuOpen";

import "./sass/main.css";
import Header from "./comps/Header";
import Banner from "./comps/Banner";
import TopSale from "./comps/TopSale";
import SpecialPrice from "./comps/SpecialPrice";
import NewPhones from "./comps/NewPhones";
import Ads from "./comps/Ads";
import LatestBlogs from "./comps/LatestBlogs";
import Footer from "./comps/Footer";

import ProductPage from "./comps/productPage/ProductPage";
import CartPage from "./comps/cartPage/CartPage";

import { useSelector, useDispatch } from "react-redux";
import { setOpenMenu } from "./redux/actions";

const App = () => {
   const { openMenu } = useSelector((state) => state);
   const dispatch = useDispatch();

   const [windowWidth, setWindowWidth] = useState(window.innerWidth);

   const newPhoneRef = useRef(null);
   const topSaleRef = useRef(null);
   const SpecialPriceRef = useRef(null);
   const footerRef = useRef(null);

   useEffect(() => {
      window.addEventListener("click", () => dispatch(setOpenMenu(false)));
      window.addEventListener("resize", () =>
         setWindowWidth(window.innerWidth)
      );

      return () => {
         window.removeEventListener("click", () =>
            dispatch(setOpenMenu(false))
         );
         window.removeEventListener("resize", () =>
            setWindowWidth(window.innerWidth)
         );
      };
   }, []);

   useEffect(() => {
      window.scrollTo(0, 0);
   }, []);

   return (
      <HashRouter>
         <Switch>
            <Route exact path="/">
               <div className="app">
                  {windowWidth < 500 ? (
                     <div
                        className={
                           openMenu
                              ? "app__links-responsive show"
                              : "app__links-responsive hide"
                        }
                     >
                        <div
                           className="links__header"
                           onClick={() => {
                              dispatch(setOpenMenu(false));
                           }}
                        >
                           <span className="icon">
                              <MenuOpenIcon />
                           </span>
                        </div>
                        <span onClick={() => window.scrollTo(0, 0)}>Home</span>
                        <span
                           onClick={() => topSaleRef.current.scrollIntoView()}
                        >
                           Top Sale
                        </span>
                        <span
                           onClick={() =>
                              SpecialPriceRef.current.scrollIntoView()
                           }
                        >
                           Special Price
                        </span>
                        <span
                           onClick={() => newPhoneRef.current.scrollIntoView()}
                        >
                           New Phones
                        </span>
                        <span
                           onClick={() => footerRef.current.scrollIntoView()}
                        >
                           More Info
                        </span>
                     </div>
                  ) : null}
                  <div className="app__header">
                     <Header
                        showMenu={true}
                        showLinks={true}
                        newPhoneRef={newPhoneRef}
                        topSaleRef={topSaleRef}
                        SpecialPriceRef={SpecialPriceRef}
                     />
                  </div>
                  <div className="app__banner">
                     <Banner />
                  </div>
                  <div className="app__topSale" ref={topSaleRef}>
                     <TopSale />
                  </div>
                  <div className="app__specialPrice" ref={SpecialPriceRef}>
                     <SpecialPrice />
                  </div>
                  <div className="app__ads">
                     <Ads />
                  </div>
                  <div className="app__newPhones" ref={newPhoneRef}>
                     <NewPhones />
                  </div>
                  <div className="app__latestBlogs">
                     <LatestBlogs />
                  </div>
                  <div className="app__footer" ref={footerRef}>
                     <Footer />
                  </div>
               </div>
            </Route>
            <Route exact path="/product/:productID/:fromPage?">
               <ProductPage />
            </Route>
            <Route to="/cart">
               <CartPage />
            </Route>
            <Route to="/notFound">
               <h1>Page Not Found</h1>
            </Route>
            <Redirect to="/notFound" />
         </Switch>
      </HashRouter>
   );
};

export default App;
