import React, { useEffect } from "react";

import Header from "../Header";
import Details from "./Details";
import TopSale from "../TopSale";
import Footer from "../Footer";

const ProductPage = () => {
   useEffect(() => {
      document.title = "Product Details";
   }, []);

   return (
      <div className="productPage">
         <div className="productPage__header">
            <Header />
         </div>
         <div className="productPage__details">
            <Details />
         </div>
         <div className="productPage__description">
            <div className="container my-4">
               <div className="title py-2">
                  <strong>Product Description</strong>
               </div>
               <p>
                  Lorem ipsum dolor, sit amet consectetur adipisicing elit.
                  Officiis cupiditate accusamus alias dolorem explicabo, iure
                  facere error officia odit consequatur velit rerum facilis
                  dolorum reiciendis repellat dolor quod, eaque odio!
               </p>
               <p>
                  Lorem ipsum dolor, sit amet consectetur adipisicing elit.
                  Officiis cupiditate accusamus alias dolorem explicabo, iure
                  facere error officia odit consequatur velit rerum facilis
                  dolorum reiciendis repellat dolor quod, eaque odio!
               </p>
            </div>
         </div>
         <div className="productPage__topSale my-5">
            <TopSale />
         </div>
         <div className="productPage__footer">
            <Footer />
         </div>
      </div>
   );
};

export default ProductPage;
