import React, { useEffect } from "react";

import Header from "../Header";
import NewPhones from "../NewPhones";
import Footer from "../Footer";
import Cart from "./Cart";
import WishList from "./WishList";

import { useSelector, useDispatch } from "react-redux";

const CartPage = () => {
   const { addedToCart } = useSelector((state) => state);

   useEffect(() => {
      document.title = "Cart Details";
   }, []);

   return (
      <div className="cartPage">
         <div className="cartPage__header">
            <Header />
         </div>
         <div className="cartPage__all container">
            <div className="cartPage__wrapper row">
               <div className="wrapper__cart-wishList col-12">
                  <div className="cart">
                     <Cart />
                  </div>
                  <div className="wishList">
                     <WishList />
                  </div>
               </div>
            </div>
            <div className="all__newPhones my-5">
               <NewPhones />
            </div>
         </div>
         <div className="cartPage__footer">
            <Footer />
         </div>
      </div>
   );
};

export default CartPage;
