import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";

import DoneIcon from "@material-ui/icons/Done";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import { Button } from "@material-ui/core";

import Ratings from "../Ratings";

import { useSelector, useDispatch } from "react-redux";
import { addToWishList, refresh } from "../../redux/actions";

const Cart = () => {
   const { addedToCart, doRefresh } = useSelector((state) => state);
   const dispatch = useDispatch();

   const [cartCount, setCartCount] = useState(0);
   const [totalPrice, setTotalPrice] = useState(0);
   const [showDropDown, setShowDropDown] = useState(false);

   const handleAddToWishList = (obj) => {
      dispatch(addToWishList(obj));
      const index = addedToCart.indexOf(obj);
      addedToCart.splice(index, 1);
      /* (goto the specific index of array addedToCart, delete 1 item starting from that index) */
      dispatch(refresh(true));
   };

   const handleRemove = (obj) => {
      const index = addedToCart.indexOf(obj);
      addedToCart.splice(index, 1);
      dispatch(refresh(true));
   };

   const handleDropDown = (e) => {
      e.stopPropagation();

      if (showDropDown) {
         setShowDropDown(false);
      } else {
         setShowDropDown(true);
      }
   };

   useEffect(() => {
      setCartCount(addedToCart.length);

      let totalPrice = 0;
      eval(
         addedToCart.forEach((obj) => {
            return (totalPrice += obj.price);
         })
      );
      setTotalPrice(totalPrice);

      dispatch(refresh(false));
   }, [doRefresh, addedToCart]);

   useEffect(() => {
      window.addEventListener("click", () => setShowDropDown(false));

      return () => {
         window.removeEventListener("click", () => setShowDropDown(false));
      };
   }, []);

   return (
      <>
         <div className="cart__title">
            <span>
               Cart <span>({cartCount})</span>
            </span>
            <Button onClick={handleDropDown}>
               <span>Subtotal</span>
               <span>
                  <ExpandMoreIcon />
               </span>
            </Button>
         </div>
         <div className="forDropDown">
            <div
               className={
                  showDropDown
                     ? "dropDown-subtotal show"
                     : "dropDown-subtotal hide"
               }
               onClick={(e) => e.stopPropagation()}
            >
               <span>
                  <span className="icon mx-2">
                     <DoneIcon />
                  </span>
                  <span>Your order is eligible for FREE Delivery</span>
               </span>
               <hr />
               <div className="dropDown__wrapper">
                  <div className="itemCount">
                     <span>Subtotal ( {cartCount} Items ) :</span>
                  </div>
                  <div className="totalSum">
                     <span>${totalPrice}</span>
                  </div>
                  <Button
                     variant="contained"
                     color="secondary"
                     onClick={() =>
                        alert(
                           "You'll be redirected to a page where you can pay online OR provide location for delivery"
                        )
                     }
                  >
                     Order Now
                  </Button>
               </div>
            </div>
            <div className="cart__allCartItems">
               {addedToCart.length > 0 ? (
                  addedToCart.map((product, index) => {
                     return (
                        <div className="wrapper row" key={index}>
                           <div className="image col-lg-3 col-md-3 col-8 mx-auto my-2">
                              <img
                                 src={process.env.PUBLIC_URL + product.path}
                                 alt="product photo"
                                 className="img-fluid"
                              />
                           </div>
                           <div className="others col-lg-7 col-md-7 col-10">
                              <div className="name">
                                 <span>{product.name}</span>
                              </div>
                              <div className="brand">
                                 <span>by {product.brand}</span>
                              </div>
                              <div className="ratings my-3">
                                 <Ratings rating={product.rating} />
                                 <span className="text">
                                    10,000 ratings | 20 reviews
                                 </span>
                              </div>
                              <div className="qty-color row">
                                 <div className="qty col-lg-3 col-md-4 col-12">
                                    <strong>Quantity: </strong>
                                    <span>{product.quantity}</span>
                                 </div>
                                 <div className="color col-lg-9 col-md-8 col-12">
                                    <strong>Color: </strong>
                                    <span
                                       className="roundedColor"
                                       style={{
                                          backgroundColor: product.color,
                                       }}
                                    ></span>
                                 </div>
                                 <div className="acitons col-12">
                                    <div className="edit-remove">
                                       <div className="editBtn">
                                          <Link
                                             to={`/product/${product.id}/cartPage`}
                                          >
                                             <Button variant="outlined">
                                                Edit
                                             </Button>
                                          </Link>
                                       </div>
                                       <div className="removeBtn">
                                          <Button
                                             variant="outlined"
                                             className="text-danger"
                                             onClick={() =>
                                                handleRemove(product)
                                             }
                                          >
                                             Remove
                                          </Button>
                                       </div>
                                    </div>
                                    <div className="saveFrLater-buy">
                                       <div className="buyBtn">
                                          <Button
                                             variant="outlined"
                                             className="text-primary"
                                             onClick={() =>
                                                alert(
                                                   "You'll be redirected to a page where you can pay online OR provide the location for delivery"
                                                )
                                             }
                                          >
                                             Buy
                                          </Button>
                                       </div>
                                       <div className="saveForLaterBtn">
                                          <Button
                                             variant="outlined"
                                             className="text-success"
                                             onClick={() =>
                                                handleAddToWishList(product)
                                             }
                                          >
                                             Save For Later
                                          </Button>
                                       </div>
                                    </div>
                                 </div>
                              </div>
                           </div>
                           <div className="price col-2">
                              <span>${product.price}</span>
                           </div>
                        </div>
                     );
                  })
               ) : (
                  <div className="emptyMsg">
                     <div className="image w-50 mx-auto">
                        <img
                           src={
                              process.env.PUBLIC_URL + "/Images/empty_cart.webp"
                           }
                           alt="empty cart photo"
                           className="img-fluid"
                        />
                     </div>
                     <div className="text">
                        <p>Your Shopping Cart is Empty !</p>
                     </div>
                  </div>
               )}
            </div>
         </div>
      </>
   );
};

export default Cart;
