import React, { useState, useEffect } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { Link } from "react-router-dom";

import { Button } from "@material-ui/core";

import { productsPath } from "./ImageData";
import Ratings from "./Ratings";

import { useSelector, useDispatch } from "react-redux";
import { addToCart, refresh } from "../redux/actions";

const NewPhones = () => {
   const { addedToCart, doRefresh } = useSelector((state) => state);
   const dispatch = useDispatch();

   const [allCartItemID, setAllCartItemID] = useState([]);
   const [windowWidth, setWindowWidth] = useState(window.innerWidth);

   const sliderSettings = {
      dots: true,
      infinite: true,
      speed: 500,
      slidesToShow: windowWidth > 500 ? 3 : 1,
      slidesToScroll: 1,
   };

   const handleAddToCart = (id, name, brand, rating, path, price) => {
      dispatch(
         addToCart({
            id: id,
            color: "white",
            quantity: 1,
            name: name,
            brand: brand,
            rating: rating,
            path: path,
            price: price,
         })
      );
   };

   useEffect(() => {
      let arr = [];
      addedToCart.forEach((product) => {
         arr.push(product.id);
      });
      setAllCartItemID(arr);
      dispatch(refresh(false));
   }, [addedToCart, doRefresh]);

   useEffect(() => {
      window.addEventListener("resize", () => {
         setWindowWidth(window.innerWidth);
      });

      return () => {
         window.addEventListener("resize", () => {
            setWindowWidth(window.innerWidth);
         });
      };
   }, []);

   return (
      <div className="newPhones container">
         <div className="newPhones__wrapper">
            <div className="newPhones__title">
               <span>New Phones</span>
            </div>
            <div className="newPhones__products">
               <Slider {...sliderSettings}>
                  {productsPath.map((product, index) => {
                     let matching = 0;
                     allCartItemID.forEach((id) => {
                        if (id === product.id) {
                           matching += 1;
                        }
                     });

                     return (
                        <div className="singleProduct" key={index}>
                           <div className="image">
                              <Link to={`/product/${product.id}`}>
                                 <img
                                    src={
                                       process.env.PUBLIC_URL + product.pathName
                                    }
                                    alt="product"
                                 />
                              </Link>
                           </div>
                           <div className="productName">
                              <span>{product.name}</span>
                           </div>
                           <div className="ratings">
                              <Ratings rating={product.rating} />
                           </div>
                           <div className="productPrice">
                              <strong>$</strong>
                              <strong>{product.price}</strong>
                           </div>
                           <div className="addToCart_Btn">
                              {matching > 0 ? (
                                 <Button
                                    variant="contained"
                                    className="addedToCart"
                                    disabled
                                 >
                                    Added To Cart
                                 </Button>
                              ) : (
                                 <Button
                                    variant="contained"
                                    onClick={() =>
                                       handleAddToCart(
                                          product.id,
                                          product.name,
                                          product.brandName,
                                          product.rating,
                                          product.pathName,
                                          product.price
                                       )
                                    }
                                 >
                                    Add To Cart
                                 </Button>
                              )}
                           </div>
                        </div>
                     );
                  })}
               </Slider>
            </div>
         </div>
      </div>
   );
};

export default NewPhones;
