import React from "react";

const Price = ({ price }) => {
   return (
      <>
         <div>
            <div>M.R.P:</div>
            <div>
               <strike>${price}</strike>
            </div>
         </div>
         <div>
            <div>Deal Price:</div>
            <div className="dealPrice">${price - 19}</div>
            <div>inclusive of all taxes</div>
         </div>
         <div>
            <div>You save:</div>
            <div className="savePrice">$19.00</div>
         </div>
      </>
   );
};

export default Price;
