export let productsPath = [];
export let bannersPath = ["/Images/banners/Banner2.webp"];
export let adsPath = [];
export let blogsPath = [];

const deviceNames = [
   "",
   "Samsung device 1",
   "Xiaomi device 1",
   "Mi device 2",
   "Redmi device 3",
   "Poco device 4",
   "Redmi device 5",
   "",
   "Xiaomi device 6",
   "",
   "Mi device 7",
   "Samsung device 2",
   "Samsung device 3",
   "Apple device 1",
   "Iphone device 2",
   "Iphone device 3",
];
const brands = [
   "",
   "Samsung",
   "Redmi",
   "Redmi",
   "Redmi",
   "Redmi",
   "Redmi",
   "",
   "Redmi",
   "",
   "Redmi",
   "Samsung",
   "Samsung",
   "Apple",
   "Apple",
   "Apple",
];

for (let i = 1; i <= 15; i++) {
   if (i !== 7 && i !== 9) {
      productsPath.push({
         id: brands[i] + i,
         name: deviceNames[i],
         brandName: brands[i],
         pathName: `/Images/products/${i}.webp`,
         price:
            Math.floor(Math.random() * 200) + 100 /* num between 100 to 300 */,
         rating: Math.floor(Math.random() * 6),
      });
   }
}
for (let i = 1; i <= 2; i++) {
   bannersPath.push(`/Images/banners/Banner${i}.webp`);
}
for (let i = 1; i <= 2; i++) {
   adsPath.push(`/Images/ads/ads${i}.webp`);
}
for (let i = 1; i <= 3; i++) {
   blogsPath.push(`/Images/blog/blog${i}.webp`);
}
