export const addToCart = ({
   id,
   color,
   quantity,
   name,
   brand,
   rating,
   path,
   price,
}) => {
   return {
      type: "ADD_TO_CART",
      id: id,
      color: color,
      quantity: quantity,
      name: name,
      brand: brand,
      rating: rating,
      path: path,
      price: price,
   };
};

export const addToWishList = ({
   id,
   color,
   quantity,
   name,
   brand,
   rating,
   path,
   price,
}) => {
   return {
      type: "ADD_TO_WISHLIST",
      id: id,
      color: color,
      quantity: quantity,
      name: name,
      brand: brand,
      rating: rating,
      path: path,
      price: price,
   };
};

export const refresh = (value) => {
   return {
      type: "REFRESH",
      value: value,
   };
};

export const setOpenMenu = (value) => {
   return {
      type: "OPEN_MENU",
      value: value,
   };
};
