import React, { useState, useEffect } from "react";
import { Link, useParams } from "react-router-dom";

import { productsPath } from "../ImageData";

import Guarantee from "./Guarantee";
import Price from "./Price";
import NameAndRatings from "./NameAndRatings";
import Delivery from "./Delivery";

import { Button } from "@material-ui/core";
import CheckIcon from "@material-ui/icons/Check";
import ExpandLessIcon from "@material-ui/icons/ExpandLess";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";

import { useDispatch, useSelector } from "react-redux";
import { addToCart, refresh } from "../../redux/actions";

const Details = () => {
   const { addedToCart, doRefresh } = useSelector((state) => state);
   const dispatch = useDispatch();

   const [product, setProduct] = useState({});
   const [quantity, setQuantity] = useState(1);
   const [color, setColor] = useState("white");
   const [existInCart, setExistInCart] = useState(false);

   const [selectedRed, setSelectedRed] = useState(false);
   const [selectedBlack, setSelectedBlack] = useState(false);
   const [selectedBlue, setSelectedBlue] = useState(false);

   const { productID, fromPage } = useParams();

   const handleColor = (color) => {
      if (color === "red") {
         if (selectedRed) {
            setSelectedRed(false);
            setColor("white");
         } else {
            setSelectedRed(true);
            setColor(color);
         }
      } else {
         setSelectedRed(false);
      }

      if (color === "black") {
         if (selectedBlack) {
            setSelectedBlack(false);
            setColor("white");
         } else {
            setSelectedBlack(true);
            setColor(color);
         }
      } else {
         setSelectedBlack(false);
      }

      if (color === "blue") {
         if (selectedBlue) {
            setSelectedBlue(false);
            setColor("white");
         } else {
            setSelectedBlue(true);
            setColor(color);
         }
      } else {
         setSelectedBlue(false);
      }
   };

   const handleAddToCart = () => {
      dispatch(
         addToCart({
            id: productID,
            color: color,
            quantity: quantity,
            name: product.name,
            brand: product.brandName,
            rating: product.rating,
            path: product.pathName,
            price: product.price,
         })
      );

      setColor("white");
      setQuantity(0);

      setSelectedRed(false);
      setSelectedBlue(false);
      setSelectedBlack(false);
   };

   const handleUpdateBtn = () => {
      let index = null;
      addedToCart.forEach((obj) => {
         if (obj.id === productID) {
            index = addedToCart.indexOf(obj);
         }
      });

      addedToCart.splice(index, 1, {
         id: productID,
         color: color,
         quantity: quantity,
         name: product.name,
         brand: product.brand,
         rating: product.rating,
         path: product.path,
         price: product.price,
      });

      dispatch(refresh(true));

      setColor("white");
      setQuantity(0);

      setSelectedRed(false);
      setSelectedBlue(false);
      setSelectedBlack(false);
   };

   useEffect(() => {
      setExistInCart(false);
      const filteredArray = productsPath.filter((product) => {
         return product.id === productID;
      });
      filteredArray.forEach((obj) => {
         setProduct(obj);
      });

      /* checking if the product added to cart */
      addedToCart.forEach((cartProduct) => {
         if (cartProduct.id === productID) {
            setExistInCart(true);
         }
      });

      window.scrollTo(0, 0);
      dispatch(refresh(false));
   }, [productID, addedToCart, doRefresh]);

   useEffect(() => {
      if (fromPage === "cartPage") {
         addedToCart.forEach((obj) => {
            if (obj.id === productID) {
               setProduct(obj);
               setQuantity(obj.quantity);

               if (obj.color === "red") {
                  setSelectedRed(true);
               } else if (obj.color === "black") {
                  setSelectedBlack(true);
               } else if (obj.color === "blue") {
                  setSelectedBlue(true);
               } else {
                  return null;
               }
            }
         });
      }
   }, [fromPage]);

   return (
      <div className="details container">
         <div className="details__wrapper row">
            <div className="fullImage col-lg-6 col-md-6 col-12">
               <div className="image">
                  <img
                     src={
                        process.env.PUBLIC_URL +
                        (product.pathName || product.path)
                     }
                     alt="product photo"
                     className="img-fluid"
                  />
               </div>
               <div className="fullImage__buttons row">
                  <div className="col-lg-6 col-md-6 col-12 my-1">
                     <Button
                        variant="contained"
                        color="secondary"
                        style={{ width: "100%" }}
                        onClick={() => {
                           alert(
                              "You'll be redirected to a page where you can pay online OR give the location for delivery"
                           );
                        }}
                     >
                        Proceed To Buying
                     </Button>
                  </div>
                  <div className="col-lg-6 col-md-6 col-12 my-1">
                     {fromPage === "cartPage" ? (
                        <Link to="/cart">
                           <Button
                              variant="outlined"
                              style={{
                                 backgroundColor: "blue",
                                 width: "100%",
                                 color: "white",
                              }}
                              onClick={handleUpdateBtn}
                           >
                              Update
                           </Button>
                        </Link>
                     ) : existInCart ? (
                        <Button
                           variant="outlined"
                           style={{
                              background: "lightgreen",
                              width: "100%",
                              color: "white",
                           }}
                           disabled
                        >
                           Added To Cart
                        </Button>
                     ) : (
                        <Link to="/cart">
                           <Button
                              variant="outlined"
                              style={{ background: "yellow", width: "100%" }}
                              onClick={handleAddToCart}
                           >
                              Add To Cart
                           </Button>
                        </Link>
                     )}
                  </div>
               </div>
            </div>
            <div className="allInfo col-lg-6 col-md-6 col-12 p-3">
               <div className="allInfo__productNameAndRatings">
                  <NameAndRatings
                     name={product.name}
                     brand={product.brandName || product.brand}
                     rating={product.rating}
                  />
               </div>
               <div className="allInfo__price my-4">
                  <Price price={product.price} />
               </div>
               <div className="allInfo__guarantee">
                  <Guarantee />
               </div>
               <div className="allInfo__delivery">
                  <Delivery rating={product.rating} />
               </div>
               <div className="colorAndQuantity row">
                  <div className="color col-lg-6 col-md-12 col-12 my-2">
                     <div>Color</div>
                     <div>
                        <span
                           className="bg-danger"
                           onClick={() => handleColor("red")}
                        >
                           <CheckIcon
                              className={selectedRed ? null : "d-none"}
                           />
                        </span>
                     </div>
                     <div>
                        <span
                           className="bg-dark"
                           onClick={() => handleColor("black")}
                        >
                           <CheckIcon
                              className={selectedBlack ? null : "d-none"}
                           />
                        </span>
                     </div>
                     <div>
                        <span
                           className="bg-primary"
                           onClick={() => handleColor("blue")}
                        >
                           <CheckIcon
                              className={selectedBlue ? null : "d-none"}
                           />
                        </span>
                     </div>
                  </div>
                  <div className="quantity col-lg-6 col-md-12 col-12 my-2">
                     <div>Quantity</div>
                     <div className="qtyInput mx-2">
                        <span
                           className="upIcon"
                           onClick={() => {
                              if (quantity < 10) {
                                 setQuantity(quantity + 1);
                              }
                           }}
                        >
                           <ExpandLessIcon />
                        </span>
                        <input type="text" disabled value={quantity} />
                        <span
                           className="downIcon"
                           onClick={() => {
                              if (quantity > 1) {
                                 setQuantity(quantity - 1);
                              }
                           }}
                        >
                           <ExpandMoreIcon />
                        </span>
                     </div>
                  </div>
               </div>
            </div>
         </div>
      </div>
   );
};

export default Details;
