import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";

import { Button } from "@material-ui/core";

import Ratings from "../Ratings";

import { useSelector, useDispatch } from "react-redux";
import { addToCart, refresh } from "../../redux/actions";

const WishList = () => {
   const { addedToWishList, doRefresh } = useSelector((state) => state);
   const dispatch = useDispatch();

   const [wishListCount, setWishListCount] = useState(0);

   const handleAddToCart = (obj) => {
      dispatch(addToCart(obj));
      const index = addedToWishList.indexOf(obj);
      addedToWishList.splice(index, 1);
      /* (goto the specific index of array addedToCart, delete 1 item starting from that index) */
      dispatch(refresh(true));
   };

   const handleRemove = (obj) => {
      const index = addedToWishList.indexOf(obj);
      addedToWishList.splice(index, 1);
      dispatch(refresh(true));
   };

   useEffect(() => {
      setWishListCount(addedToWishList.length);
      dispatch(refresh(false));
   }, [doRefresh, addedToWishList]);

   return (
      <>
         <div className="wishList__title">
            <span>
               Wishlist <span>({wishListCount})</span>
            </span>
         </div>
         <div className="wishList__allCartItems">
            {addedToWishList.length > 0 ? (
               addedToWishList.map((product, index) => {
                  return (
                     <div className="wrapper row" key={index}>
                        <div className="image col-lg-3 col-md-3 col-8 mx-auto my-2">
                           <img
                              src={process.env.PUBLIC_URL + product.path}
                              alt="product photo"
                              className="img-fluid"
                           />
                        </div>
                        <div className="others col-lg-7 col-md-7 col-10">
                           <div className="name">
                              <span>{product.name}</span>
                           </div>
                           <div className="brand">
                              <span>by {product.brand}</span>
                           </div>
                           <div className="ratings my-3">
                              <Ratings rating={product.rating} />
                              <span className="text">
                                 10,000 ratings | 20 reviews
                              </span>
                           </div>
                           <div className="qty-color row">
                              <div className="qty col-lg-3 col-md-4 col-12">
                                 <strong>Quantity: </strong>
                                 <span>{product.quantity}</span>
                              </div>
                              <div className="color col-lg-9 col-md-8 col-12">
                                 <strong>Color: </strong>
                                 <span
                                    className="roundedColor"
                                    style={{ backgroundColor: product.color }}
                                 ></span>
                              </div>
                              <div className="acitons col-12">
                                 <div className="remove-buy">
                                    <div className="removeBtn">
                                       <Button
                                          variant="outlined"
                                          className="text-danger"
                                          onClick={() => handleRemove(product)}
                                       >
                                          Remove
                                       </Button>
                                    </div>
                                    <div className="buyBtn">
                                       <Button
                                          variant="outlined"
                                          className="text-primary"
                                          onClick={() =>
                                             alert(
                                                "You'll be redirected to a page where you can pay online OR provide the location for delivery"
                                             )
                                          }
                                       >
                                          Buy
                                       </Button>
                                    </div>
                                 </div>
                                 <div className="addToCart">
                                    <div className="addToCartBtn">
                                       <Button
                                          variant="outlined"
                                          className="text-success"
                                          onClick={() =>
                                             handleAddToCart(product)
                                          }
                                       >
                                          Add To Cart
                                       </Button>
                                    </div>
                                 </div>
                              </div>
                           </div>
                        </div>
                        <div className="price col-2">
                           <span>${product.price}</span>
                        </div>
                     </div>
                  );
               })
            ) : (
               <div className="emptyMsg">
                  <div className="image w-50 mx-auto">
                     <img
                        src={process.env.PUBLIC_URL + "/Images/empty_cart.webp"}
                        alt="empty cart photo"
                        className="img-fluid"
                     />
                  </div>
                  <div className="text">
                     <p>Your Wishlist is Empty !</p>
                  </div>
               </div>
            )}
         </div>
      </>
   );
};

export default WishList;
