import { combineReducers } from "redux";

const addedToCart = (state = [], action) => {
   if (action.type === "ADD_TO_CART") {
      return (state = [
         ...state,
         {
            id: action.id,
            color: action.color,
            quantity: action.quantity,
            name: action.name,
            brand: action.brand,
            rating: action.rating,
            path: action.path,
            price: action.price,
         },
      ]);
   } else {
      return state;
   }
};

const addedToWishList = (state = [], action) => {
   if (action.type === "ADD_TO_WISHLIST") {
      return (state = [
         ...state,
         {
            id: action.id,
            color: action.color,
            quantity: action.quantity,
            name: action.name,
            brand: action.brand,
            rating: action.rating,
            path: action.path,
            price: action.price,
         },
      ]);
   } else {
      return state;
   }
};

const doRefresh = (state = false, action) => {
   if (action.type === "REFRESH") {
      return (state = action.value);
   } else {
      return state;
   }
};

const openMenu = (state = false, action) => {
   if (action.type === "OPEN_MENU") {
      return (state = action.value);
   } else {
      return state;
   }
};

export default combineReducers({
   addedToCart,
   addedToWishList,
   doRefresh,
   openMenu,
});
