import React from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import { Button } from "@material-ui/core";

import { blogsPath } from "./ImageData";

const LatestBlogs = () => {
   const sliderSettings = {
      dots: false,
      arrows: false,
      speed: 500,
      infinite: true,
      slidesToShow: window.innerWidth > 770 ? 3 : 1,
      slidesToScroll: 1,
   };

   return (
      <div className="latestBlogs container">
         <div className="latestBolgs__wrapper">
            <div className="latestBlogs__title">
               <span>Latest Blogs</span>
            </div>
            <div className="latestBlogs__blogs">
               <Slider {...sliderSettings}>
                  {blogsPath.map((path, index) => {
                     return (
                        <div className="singleBlog" key={index}>
                           <div className="wrapper">
                              <div className="title">
                                 <span>Upcoming Mobiles</span>
                              </div>
                              <div className="blogImage">
                                 <img
                                    src={process.env.PUBLIC_URL + path}
                                    alt="Blog Photo"
                                    className="img-fluid mx-auto"
                                 />
                              </div>
                              <div className="description">
                                 Lorem, ipsum dolor sit amet consectetur
                                 adipisicing elit. Dignissimos, quasi ab
                                 deleniti ipsam inventore nostrum? Nisi quos
                                 consequuntur quasi unde!
                              </div>
                              <div className="button">
                                 <Button
                                    variant="contained"
                                    color="primary"
                                    onClick={() => {
                                       alert(
                                          "You can goto a page where you've written blogs or some writings about the upcoming mobile"
                                       );
                                    }}
                                 >
                                    Go Anywhere
                                 </Button>
                              </div>
                           </div>
                        </div>
                     );
                  })}
               </Slider>
            </div>
         </div>
      </div>
   );
};

export default LatestBlogs;
