import React from "react";

import Ratings from "../Ratings";

const NameAndRatings = ({ name, brand, rating }) => {
   return (
      <>
         <p className="name">{name}</p>
         <p className="brand">by {brand}</p>
         <div className="ratings">
            <Ratings rating={rating} />
            <span
               className="link"
               onClick={() => {
                  alert(
                     "You can goto a page where your customers comments & reviews will be shown"
                  );
               }}
            >
               10,000 ratings | 12 comments
            </span>
         </div>
      </>
   );
};

export default NameAndRatings;
