import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";

import ShoppingCartIcon from "@material-ui/icons/ShoppingCart";
import ListIcon from "@material-ui/icons/List";

import { useSelector, useDispatch } from "react-redux";
import { refresh, setOpenMenu } from "../redux/actions";

const Header = ({
   showLinks,
   showMenu,
   newPhoneRef,
   topSaleRef,
   SpecialPriceRef,
}) => {
   const { addedToCart, doRefresh } = useSelector((state) => state);
   const dispatch = useDispatch();

   const [itemCount, setItemCount] = useState(0);
   const [windowWidth, setWindowWidth] = useState(window.innerWidth);
   const [animateCartIcon, setAnimateCartIcon] = useState(false);

   useEffect(() => {
      setItemCount(addedToCart.length);
      dispatch(refresh(false));
   }, [doRefresh, addedToCart]);

   useEffect(() => {
      setAnimateCartIcon(true);
      setTimeout(() => {
         setAnimateCartIcon(false);
      }, 2500);
   }, [itemCount]);

   useEffect(() => {
      window.addEventListener("resize", () => {
         setWindowWidth(window.innerWidth);
      });

      return () => {
         window.removeEventListener("resize", () => {
            setWindowWidth(window.innerWidth);
         });
      };
   }, []);

   return (
      <div className="header container-fluid">
         {showMenu ? (
            <div
               className="header__menuIcon d-none"
               onClick={(e) => {
                  e.stopPropagation();
                  dispatch(setOpenMenu(true));
               }}
            >
               <span>
                  <ListIcon />
               </span>
            </div>
         ) : null}
         <div className="header__title">
            <Link to="/">
               <span>Mobile Shopee</span>
            </Link>
         </div>
         {showLinks ? (
            windowWidth > 500 ? (
               <div className="header__links">
                  <span onClick={() => window.scrollTo(0, 0)}>Home</span>
                  <span onClick={() => topSaleRef.current.scrollIntoView()}>
                     Top Sale
                  </span>
                  <span
                     onClick={() => SpecialPriceRef.current.scrollIntoView()}
                  >
                     Special Price
                  </span>
                  <span onClick={() => newPhoneRef.current.scrollIntoView()}>
                     New Phones
                  </span>
               </div>
            ) : null
         ) : null}
         {animateCartIcon ? (
            <Link to="/cart">
               <div className="header__shoppingCartIcon animate__animated animate__rubberBand">
                  <div className="icon">
                     <ShoppingCartIcon />
                  </div>
                  <div className="itemCount">{itemCount}</div>
               </div>
            </Link>
         ) : (
            <Link to="/cart">
               <div className="header__shoppingCartIcon">
                  <div className="icon">
                     <ShoppingCartIcon />
                  </div>
                  <div className="itemCount">{itemCount}</div>
               </div>
            </Link>
         )}
      </div>
   );
};

export default Header;
